import api from "../../axios";

// 
const getCityInfo = (name) => {
  return api.get("/area/info", {
    params: {
      name,
    },
  });
};

// 
const getCities = (level = 1) => {
  return api.get("/area/city", {
    params: {
      level,
    },
  });
};

// 
const getHotCity = () => {
  return api.get("/area/hot");
};

export { getCityInfo, getCities, getHotCity };
